.itemContainer {
  overflow: hidden;
  width: 100%;
}

.item {
  text-align: center;
  color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: grab;
  border: 2px solid transparent;
  border-radius: 10px;
  min-width: 0;
}

.item[data-selected] {
  border-color: #3f51b5;
}

.toolbar {
  display: flex;
  padding: 0.5rem 0;
  align-items: center;
}
