.item {
  display: flex;
  flex-direction: column;
  border: solid 2px var(--primary);
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin: 0.5rem 1rem;
}

.description {
  display: flex;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.title {
  color: var(--primary);
  margin-bottom: 0.25rem;
}

.time {
  font-size: 0.75rem;
  color: var(--primary);
  display: flex;
  align-items: center;
  margin-left: 0.5rem;
}

.time svg {
  margin-right: 0.25rem;
}
