.container {
  padding: 0.5rem;
  overflow: scroll;
  scrollbar-width: thin;
}

.titleBar {
  display: flex;
  justify-content: space-between;
}

.title {
  font-size: 24px;
  color: #3f51b5;
}

.section {
  margin: 0.375rem 0;
  padding: 0.5rem;
}

.sectionTitle {
  text-align: center;
  margin-top: 0.25rem;
  margin-bottom: 0.5rem;
  border-bottom: solid 1px rgb(192, 192, 192);
}
