.badge {
  display: flex;
  align-items: center;
  border: solid 2px var(--statusColor, transparent);
  border-radius: 0.5rem;
  align-self: start;
  padding: 0.125rem 0.375rem;
  font-weight: 600;
  color: var(--statusColor, black);
}

.icon {
  margin-right: 0.25rem;
}

.badge[data-status='Running'] {
  --statusColor: #3f51b5;
}

.badge[data-status='Success'] {
  --statusColor: #4caf50;
}

.badge[data-status='Failed'] {
  --statusColor: #f44336;
}
