.toolbar {
  display: flex;
  align-items: center;
}

.disabledCrumb {
  cursor: default;
  color: red;
}

.disabledCrumb:hover {
  text-decoration: none;
}

.button:hover {
  background-color: #303f9f !important;
}

.saveButton {
  composes: button;
  margin-left: 0.25rem !important;
}

.labelHint {
  font-weight: lighter;
  font-style: italic;
  font-size: 1rem;
}
